  export default {
    name: 'assetsWrapper',
    data (){
      return {
        defaultVal: '本月佣金明细',
        all: 0,//总金额
        cash: 0,//累计提现
        wait: 0,//带发放
        list: [],
        month: 0,
      }
    },
    computed:{

    },
    methods: {
      showToastTxtOnly(text) {
        this.toast = this.$createToast({
          txt: text,
          type: 'txt'
        })
        this.toast.show()
      },
      getAssetsData(date){
        this.http.get(this.ports.me.distribution+'?date='+date, res =>{
          this.$store.commit('changeLoading',false)
          console.log(res)
          if(res.success){
            let data = res.data
            this.all = data.all
            this.month = data.month
            this.cash = data.cash
            this.list = data.list.res
            let tList = data.list
            if(JSON.stringify(tList) == '{}'){
              this.list = []
//              this.showToastTxtOnly('无数据...')
            }
          }else{
              this.list = []
//            this.showToastTxtOnly(res.msg)
          }
        })
      },
      showDatePicker() {
        if (!this.datePicker) {
          this.datePicker = this.$createDatePicker({
            title: '日期选择',
            min: new Date(2019, 1),
            max: new Date(2020, 9),
            value: new Date(),
            format: {
              year: 'YYYY',
              month: 'MM',
            },
            columnCount: 2,
            onSelect: this.selectHandle,
            onCancel: this.cancelHandle
          })
        }

        this.datePicker.show()
      },
      selectHandle(date, selectedVal, selectedText) {
        console.log(date,selectedVal,selectedText)
        this.defaultVal = selectedText.join('-')
        this.getAssetsData(this.defaultVal)
//        this.$createDialog({
//          type: 'warn',
//          content: `Selected Item: <br/> - date: ${date} <br/> - value: ${selectedVal.join(', ')} <br/> - text: ${selectedText.join(' ')}`,
//          icon: 'cubeic-alert'
//        }).show()
      },
      cancelHandle() {
        this.$createToast({
          type: 'correct',
          txt: 'Picker canceled',
          time: 1000
        }).show()
      }
    },
    mounted (){
      this.getAssetsData("")
    }
  }



